.infoWrapper {
    position: relative;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-end;
    align-content: stretch;
    align-items: flex-end;
    height: 1px;
}

.bubble {
    bottom: 4vh;
    width: 300px;
    height: 250px;
    border: 4px solid #000000;
    padding: 30px;
    text-align: right;
    color: #00bfb6;
    background: #BAB8B8;
    font-family: arial;
    position: absolute;
    order: 0;
    flex: 0 1 auto;
    align-self: auto;
    z-index: 2;
}

.bubble:after {
    content: "";
    width: 0px;
    height: 0px;
    position: absolute;
    border-left: 10px solid transparent;
    border-right: 10px solid #BAB8B8;
    border-top: 10px solid #BAB8B8;
    border-bottom: 10px solid transparent;
    right: 24px;
    bottom: -13px;
}

.bubble:before {
    content: "";
    width: 0px;
    height: 0px;
    position: absolute;
    border-left: 10px solid transparent;
    border-right: 10px solid #000000;
    border-top: 10px solid #000000;
    border-bottom: 10px solid transparent;
    right: 20px;
    bottom: -23px;
}

